import { Component, OnInit } from '@angular/core';
import { CommonHttpService } from '../../http/common.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
@Component({
  selector: 'app-exception-transaction-list-query',
  templateUrl: './exception-transaction-list-query.component.html',
  styleUrls: ['./exception-transaction-list-query.component.scss'],
  providers: [CommonHttpService, Commfunc]
})

export class ExceptionTransactionListQueryComponent implements OnInit {

  ch: any = CALENDAR_CN; // 英文转化为中文
  msgs: Message[] = [];  // 提示信息
  first: Number = 1;
  pageNum: Number = 1;
  pageSize: number = 10;
  total: Number;
  startDT: String = '';
  endDT: String = '';
  status: String = '';
  exceptionTransactionStatus: any[];
  tableData: any[];
  code: any[];

  constructor(
    public httpService: CommonHttpService,
    public commfunc: Commfunc
  ) {
    this.codeValues();
    this.exceptionTransactionStatus = this.code['exceptionTransactionStatus'];
  }

  ngOnInit() {
    this.qryTransCompReg();
  }

  qryTransCompReg() {
    const params = {
      startDT: this.commfunc.transDateN(this.startDT),
      endDT: this.commfunc.transDateN(this.endDT),
      status: this.status,
      pageNum: this.pageNum,
      pageSize: this.pageSize
    };
    this.httpService.qryTransCompReg(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.transCompList && data.transCompList.length != 0) {
          data.transCompList.forEach(element => {
            for (const k in element) {
              if (k == 'regDate') {
                element[k] = this.commfunc.transDateN(element[k]);
              };
              if (k == 'transTime') {
                element[k] = this.commfunc.transDateN(element[k]);
              };
            }
          });
        }
        this.tableData = data.transCompList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.code ? data.returnCode.code : '调用服务失败！' }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    });
  }

  immeExecTransComp(col) {
    const params = {
      serid: col.serid
    };
    this.httpService.immeExecTransComp(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.code ? data.returnCode.code : '调用服务成功！' }];
      } else {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.code ? data.returnCode.code : '调用服务失败！' }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
    });
  }

  reset() {
    this.startDT = '';
    this.endDT = '';
    this.status = '';
  }

  paginate(event) {
    this.pageSize = event.rows; // 每页显示的条数
    const currentPage = event.page + 1; // 当前页
    this.pageNum = currentPage;
    this.first = (currentPage - 1) * this.pageSize;
    this.qryTransCompReg();
  }

  codeValues() {
    this.commfunc.codeValue();
    const ret = this.commfunc.codeDatas;
    return this.code = ret; // code是自己声明的变量
  }

}