import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CommonParamsBean } from '../bean/common-params.bean';
import { ConfirmationService } from 'primeng/primeng';
import { Message, SelectItem } from 'primeng/primeng';
// import { Status, ParamGroupId, ParamTypeId } from '../../../constant/codeValue'
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CommonHttpService } from '../../../http/common.http.service';
//公共参数
@Component({
    selector: 'common-params-item',
    templateUrl: './common-params-item.html',
    styleUrls: ['./common-params-item.scss'],
    providers: [CommonHttpService, ConfirmationService, Commfunc]
})
export class CommonParamsItem implements OnInit {
    commonParamsTitle: string = '公共参数项管理';//标题

    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    tabMesg: any = tableMessage;// 表格无数据时显示的信息
    // 表体内容
    tableData: any;
    commonParamsBean: CommonParamsBean = new CommonParamsBean()
    //角色状态
    roleStatus: any = [];

    //总页数
    total: string;
    //每页多少条
    limit: string = '10';

    //提示信息
    msgs: Message[] = [];

    //模态框的标题
    headerTitle: string = "修改";
    //模态框是否显示
    display: boolean = false;
    showModel: string;

    //修改的参数
    updateValue: any = [];
    //新增的参数
    addValue: any = [];
    //公共模块 字段所属模块
    paramGroupId: any = [];
    //公共模块 字段所属类型
    paramTypeId: any = [];
    //筛选 状态
    status: any = [];
    code: any;
    first:number=0;
    currentPage:any;
    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.status = this.code['Status'];
        this.paramGroupId = this.code['ParamGroupId'];
        this.paramTypeId = this.code['ParamTypeId']

    }

    ngOnInit() {
        this.fuzhi()
        this.commonParamsBean.limit = '10';
        this.commonParamsBean.page = '1';
        this.query();
        
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    ngOnChanges() {
        // this.fuzhi()
        // this.ngOnInit()
    }
    fuzhi() {
        this.commonParamsBean = this.inValue;
    }
    //列表查询
    query() {
        // ;
        this.commonParamsBean.page=this.currentPage;
        this.commonHttpService.queryCommonParamItemService(this.commonParamsBean).subscribe(data => {
            // ;
            if (data.returnCode.code == 'success') {
                this.tableData = data.returnCommonParamItemList;
                this.total = data.total;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //分页
    paginate(event) {
        // ;
        //每页显示的条数
        this.commonParamsBean.limit = event.rows;
        //当前页
        this.currentPage = event.page + 1 + '';
        this.commonParamsBean.page = this.currentPage;
        //调用查询的方法
        this.query();
        this.commonParamsBean.page = '1';
        this.first = event.rows * event.page;
    }

    //修改
    update(item) {
        this.headerTitle = '参数修改';
        this.display = true;
        this.showModel = '2';
        this.updateValue = item;
    }

    //修改的回调
    updateCall(param) {
        this.display = param;
        this.query();
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功' });
    }

    //删除
    delete(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                // 条件
                // ;

                let param = { paramId: item.paramId, paramItemName: item.paramItemName };
                this.commonHttpService.deleteCommonParamItemService(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '删除成功' });
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }

    //添加
    add() {
        this.headerTitle = '参数增加';
        this.display = true;
        this.showModel = '1';
        this.addValue = this.commonParamsBean;
    }

    //添加后返回的值
    addCall(param) {
        this.display = param;
        // this.roleManageBean.startRow = '1';
        this.query();
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: '添加成功' });
    }

    // 重置
    reset() {
        this.commonParamsBean.paramName = '';
        this.commonParamsBean.status = '';
        this.commonParamsBean.paramGroupId = '';
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }


    //返回的值
    // outValue(param) {
    //     this.display = param.display;
    // }

}
