import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CommonParamsBean } from '../../bean/common-params.bean';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { CommonHttpService } from '../../../../http/common.http.service';

@Component({
    selector: 'common-params-item-update',
    styleUrls: ['common-params-item-update.scss'],
    templateUrl: 'common-params-item-update.html',
    providers: [CommonHttpService]
})

export class CommonParamsItemUpdate implements OnInit {
    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    //提示信息
    msgs: Message[] = [];
    //实体
    commonParamsBean: CommonParamsBean = new CommonParamsBean();
    userform: FormGroup; //校验
    commonForm: FormGroup;
    paramItemName:string;//提交时中间量
    constructor(
        public commonHttpService: CommonHttpService,
        public fb: FormBuilder,
    ) { }

    ngOnInit() {
        this.userform = this.fb.group({
            'paramItemSequence':new FormControl('',[Validators.pattern(/^\d*$/)] ),
            'paramId': new FormControl('', ),
            'paramItemN': new FormControl('', [Validators.pattern(/^[\w]*$/)]),
            // 'newParamItemName': new FormControl('', [Validators.pattern(/^[\w]{1,20}$/), Validators.required]),
            'paramItemValue': new FormControl('', [Validators.pattern(/^[^\s\'\"]*$/), Validators.required]),
            'paramItemDesc': new FormControl('', [Validators.pattern(/^[\u4e00-\u9fa5a-zA-Z0-9\u3002\uff01\uff0c\u3001\uff1b\uff1a\u201c\u201d\u2018\u2019\uff08\uff09\u300a\u300b\u300b\u3008\u3009\u3010\u3011\u300e\u300f\u300c\u300d\ufe43\ufe44\u3014]{1,255}$/)])
        });
    }

    ngOnChanges() {
        this.fuzhi()
    }
    fuzhi() {
        this.commonParamsBean = Object.assign({},this.inValue);
        this.commonParamsBean.paramItemN = this.inValue.paramItemName;
        this.paramItemName = this.inValue.paramItemName;
    }
    //提交
    preserveClick() {
        //校验
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        if(this.commonParamsBean.paramItemN == this.paramItemName) {
            this.commonParamsBean.paramItemName=this.paramItemName;
            this.commonParamsBean.newParamItemName=this.paramItemName;
        }else {
            this.commonParamsBean.newParamItemName=this.commonParamsBean.paramItemN;
            this.commonParamsBean.paramItemN=this.paramItemName;
        }
        this.commonHttpService.modifyCommonParamItemService(this.commonParamsBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.code });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });
        this.commonParamsBean.paramItemN=this.commonParamsBean.newParamItemName;
    }
    //取消
    modifyCloseBtn() {
        this.outValue.emit(false);
        this.commonParamsBean = new CommonParamsBean();
    }

}