import { WebAppSourceBean } from './bean/web-app-source.bean';
import { Message, TreeNode } from 'primeng/primeng';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges, ChangeDetectorRef } from '@angular/core';
import { SharedBasisModule } from '../../../../../../app/sharedBasis.module';
import { CommonHttpService } from '../../http/common.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { ConfirmationService } from 'primeng/primeng';

@Component({
  selector: 'app-web-app-source',
  templateUrl: './web-app-source.component.html',
  styleUrls: ['./web-app-source.component.scss'],
  providers: [ConfirmationService]
})


/**
 * 资源树
 */
export class WebSourceComponent implements OnInit {
  tableDataCopy: any;
  copy: any;
  exportFlag: boolean = true;
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();

  tableL: any[] = [
    { sourceId: '38566124' },
    { sourceId: '' }
  ];
  //提示信息
  msgs: Message[] = [];
  //接受后台返回的数据
  treeList: TreeNode[] = [];
  tableData: any = [];
  button: any = [];
  menu: any = [];
  selectButton: any = [];
  selectMenu: any = [];
  sourceTypeOpt: any[] = [];//状态
  //选中的值
  selectedList: Array<any> = [];
  //点击的父级叔的id
  parentSourceId1: string = '';
  parentSourceDescription1: string = '';
  parentSourceTypeId1: string = '';
  data: any = [];
  addFlag: boolean = true;
  dataFlagList: any = [];
  noteName: string = '';
  selectedFiles: any;
  choosedList: any[] = [];
  checkAll: any[] = [];
  chooseDataList: any[] = [];
  constructor(
    public commonHttpService: CommonHttpService,
    public commfunc: Commfunc,
    public changeDetectorRef: ChangeDetectorRef,
    public confirmationService: ConfirmationService
  ) {

  }

  ngOnInit() {
    this.sourceTypeOpt = [{ label: "菜单", value: 'FEATURES_MENU' }, { label: "按钮", value: 'FEATURES_BUTTON' }, { label: "字段", value: 'FEATURES_FIELD' }]
    //查所有的菜单数据
    let el1 = { label: "菜单", data: "0", flag: 1, children: [], icon: 'fa ui-icon-format-align-left', sourceTypeId: 'FEATURES_MENU' };
    let list: any = [];
    let param1 = { isAll: '1' }
    this.commonHttpService.getPermSourceListByPermId(param1).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.button = data.items.button;
        this.menu = data.items.menu;
        el1.children = this.forMenuList(this.menu, this.button);
        list.push(el1);
        this.treeList = list;
        this.changeDetectorRef.detectChanges();
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }


  nodeCheck(event) {
    let param1 = { parentSourceId: event.node.data }
    this.commonHttpService.queryWebSourceByParent(param1).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.noteName = '';
        this.addFlag = false;
        this.dataFlagList = [];
        this.chooseDataList = [];
        this.choosedList = [];
        this.tableData = data.webSourceList;
        this.parentSourceId1 = event.node.data;
        this.parentSourceDescription1 = event.node.label;
        this.parentSourceTypeId1 = event.node.sourceTypeId;
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })



  }

  forMenuList(param: Array<any>, button) {
    let list: any = [];
    param.forEach(element => {
      let el = {};
      //判读是button还是menu,如果是menu前面的图标是 ui-icon-format-align-left，button前面图标是ui-icon-apps
      if (element['sourceTypeId'] == 'FEATURES_MENU') {
        el = { label: element.description, data: element.sourceId, children: [], icon: 'fa ui-icon-format-align-left' };
      } else {
        el = { label: element.description, data: element.sourceId, children: [], icon: 'fa ui-icon-apps' };
      }
      // if (element.linkUrl) {
      //   let buttonList = [];
      //   if (button[element.sourceId]) {
      //     button[element.sourceId].forEach(element => {
      //       let but = { label: element.description, data: element.sourceId, children: [], icon: 'fa ui-icon-apps' };
      //       buttonList.push(but);
      //     });
      //   }
      //   el['children'] = buttonList;
      // }
      if (this.checkMenuList(element)) {
        el['sourceTypeId'] = element.sourceTypeId;
        el['expandedIcon'] = "fa-folder-open";
        el['collapsedIcon'] = "fa-folder";
        el['icon'] = "fa ui-icon-format-align-left";
        el['children'] = this.forMenuList(element['items'], button);
      }
      list.push(el)
    })
    return list;
  }

  //菜单循环
  checkMenuList(param: Object) {
    if (param['items']) {
      return true;
    }
    return false;
  }

  //删除事件
  delete(flag, item) {
    if (this.noteName == '') {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "记录标识不能为空。" });
      return;
    }
    this.confirmationService.confirm({
      message: '确认要删除吗?',
      accept: () => {
        // 条件
        let param = { flag: flag, noteName: this.noteName, webSourceList: [item] }
        this.commonHttpService.createOrUpdateWebSource(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.meaasge });
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
        })
      }
    });
  }


  //全选
  checkAllClick(event) {
    if (event) {
      let choosedList = [];
      for (let i = 0; i < this.tableData.length; i++) {
        choosedList.push(i);
      }
      this.choosedList = JSON.parse(JSON.stringify(choosedList));
      this.chooseDataList = JSON.parse(JSON.stringify(this.tableData));
    } else {
      this.choosedList = [];
      this.chooseDataList = [];
    }
  }
  //选择
  choosed(event, ri, car) {
    let result = this.indexof(car)
    if (event) {
      this.chooseDataList.push(car)
    } else {
      if (result.flag) {
        this.chooseDataList.splice(result.index, 1);
      }
    }
  }
  //是否复制子集
  copyChild(event, car) {
    let result = this.indexof(car)
    if (event) {
      car.copyChild = 'Y';
      if (result.flag) {
        this.chooseDataList[result.index].copyChild = 'Y';
      }

    } else {
      delete car.copyChild;
      if (result.flag) {
        delete this.chooseDataList[result.index].copyChild;
      }
    }

  }
  //是否常用
  oftenFlag(event, car) {
    let result = this.indexof(car);
    if (event) {
      car['oftenFlag'] = '1';
      if (result.flag) {
        this.chooseDataList[result.index]['oftenFlag'] = '1';
      }
    } else {
      car['oftenFlag'] = null;
      if (result.flag) {
        this.chooseDataList[result.index]['oftenFlag'] = null;
      }
    }
  }
  //勾选动态权限
  powerChange(event, car) {
    let result = this.indexof(car);
    if (event) {
      car['dataFlag'] = 'Y';
      if (result.flag) {
        this.chooseDataList[result.index].dataFlag = 'Y';
      }
    } else {
      car['dataFlag'] = 'N';
      if (result.flag) {
        this.chooseDataList[result.index].dataFlag = 'N';
      }
    }
  }
  add() {
    this.tableData.push({ sourceId: this.parentSourceId1, sequenceNum: null, sourceTypeId: 'FEATURES_MENU', parentSourceId: this.parentSourceId1, description: null, linkUrl: null, dataFlag: null })
    let tableData = JSON.parse(JSON.stringify(this.tableData))
    this.tableData = tableData;
  }
  //导出
  export(flag) {
    if (JSON.stringify(this.tableData) != this.tableDataCopy) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: 'error message', detail: '请先保存再导出！' });
      return;
    }
    if (this.noteName == '') {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "记录标识不能为空。" });
      return;
    }
    let param = { flag: flag, noteName: this.noteName, webSourceList: this.chooseDataList }
    this.commonHttpService.exportWebSourceSql(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //判断存不存在
  indexof(car) {
    let flag = false;
    let index;
    for (let i = 0; i < this.chooseDataList.length; i++) {
      if (car.sourceId == this.chooseDataList[i].sourceId) {
        flag = true;
        index = i;
        break
      }
    }
    return { flag, index }
  }
  //保存
  submit(flag) {
    console.log(this.tableData)

    if (this.noteName == '') {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "记录标识不能为空。" });
      return;
    }

    for (let i = 0; i < this.tableData.length; i++) {
      if (this.tableData[i].sourceId == '' || this.tableData[i].description == '' || this.tableData[i].sequenceNum == '') {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: "资源编号、资源名称、资源序列号不能为空。" });
        return;
      }
      if (this.tableData[i].sourceId == this.parentSourceId1) {
        this.msgs = [{ severity: 'error', summary: '提示', detail: '资源编号不能和父级相同！' }];
        return
      }
    }

    let param = { flag: flag, noteName: this.noteName, webSourceList: this.tableData }
    this.commonHttpService.createOrUpdateWebSource(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.exportFlag = false;
        this.tableDataCopy = JSON.stringify(this.tableData);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  sourceChange(sourceId) {
    this.commonHttpService.queryWebSourceByParent({ sourceId }).subscribe(data => {
      if (data.returnCode.code == 'success') {
        if (data.webSourceList.length!=0) {
          this.msgs = [{ severity: 'error', summary: '提示', detail: '资源编号已存在！' }];
          return
        }
      }
    })
  }
}





















