import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CommonParamsBean } from '../../bean/common-params.bean';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { CommonHttpService } from '../../../../http/common.http.service';

@Component({
  selector: 'common-params-item-add',
  styleUrls: ['common-params-item-add.scss'],
  templateUrl: 'common-params-item-add.html',
  providers: [CommonHttpService]
})

export class CommonParamsItemAdd implements OnInit {
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  @Input() inValue: any;
  commonParamItemList: any;//传参媒介


  //提示信息
  msgs: Message[] = [];
  listValue: any;//代管发起传参列表
  commonParamsB: any
  paramItemName: string
  paramItemValue: string
  paramItemDesc: string
  paramItemSequence:string

  userform: FormGroup; //校验
  commonForm: FormGroup;
  commonParamsBean: CommonParamsBean = new CommonParamsBean();
  constructor(
    public commonHttpService: CommonHttpService,
    public fb: FormBuilder,
  ) {
    // this.commonForm = fb.group({
    //   paramItemValue: ['', Validators.required]
    // })
  }

  ngOnInit() {
    this.userform = this.fb.group({
      'paramItemSequence':new FormControl('',[Validators.pattern(/^\d*$/)] ),
      'paramItemName': new FormControl('', [Validators.pattern(/^[\w]*$/)]),
      'paramItemValue': new FormControl('', [Validators.pattern(/^[^\s\'\"]*$/), Validators.required]),
      'paramItemDesc': new FormControl('', [Validators.pattern(/^[\u4e00-\u9fa5a-zA-Z0-9\u3002\uff01\uff0c\u3001\uff1b\uff1a\u201c\u201d\u2018\u2019\uff08\uff09\u300a\u300b\u300b\u3008\u3009\u3010\u3011\u300e\u300f\u300c\u300d\ufe43\ufe44\u3014]{1,255}$/)])
    });
  }
  ngOnChanges() {
    this.fuzhi()
  }
  fuzhi() {
    this.commonParamsBean = this.inValue;
  }
  // 确定按钮
  preserveClick() {
    //校验
    for (const i in this.userform.controls) {
      this.userform.controls[i].markAsDirty();
    }
    for (const i in this.userform.controls) {
      if (!this.userform.controls[i].valid) {
        return;
      }
    }
    let obj = {};
    let commonParamItemList = []
    obj['paramItemName'] = this.paramItemName,
      obj['paramItemValue'] = this.paramItemValue,
      obj['paramItemDesc'] = this.paramItemDesc,
      obj['paramItemSequence']=this.paramItemSequence
      commonParamItemList.push(obj)
    let listValue = {
      commonParamItemList: commonParamItemList,
      paramId: this.commonParamsBean.paramId,
    }
    this.commonHttpService.addCommonParamItemService(listValue).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.outValue.emit(false);
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.code });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
    })

  }

  modifyCloseBtn() {
    this.outValue.emit(false);
    this.commonParamsBean = new CommonParamsBean();
  }

}