import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { CommonParamsBean } from './../bean/common-params.bean';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CommonHttpService } from '../../../http/common.http.service';

// import { Status, ParamGroupId, ParamTypeId } from '../../../constant/codeValue'
@Component({
    selector: 'common-params-update',
    styleUrls: ['common-params-update.scss'],
    templateUrl: 'common-params-update.html',
    providers: [CommonHttpService, Commfunc]
})

export class CommonParamsUpdate implements OnInit {
    @Input() inValue: any;

    @Output() outValue: EventEmitter<any> = new EventEmitter();
    userform: FormGroup; //校验
    commonForm: FormGroup;
    //提示信息
    msgs: Message[] = [];
    //实体
    commonParamsBean: CommonParamsBean = new CommonParamsBean();
    //公共模块 字段所属模块
    paramGroupId: any = [];;
    //公共模块 字段所属类型
    paramTypeId: any = [];;
    //筛选 状态
    status: any = [];;
    code: any;
    constructor(
        public commonHttpService: CommonHttpService,
        public fb: FormBuilder,
        public commfunc: Commfunc
    ) {

        this.codeValues() //调用方法，获取全部码值
        this.status = this.code['Status'];
        this.paramGroupId = this.code['ParamGroupId'];
        this.paramTypeId = this.code['ParamTypeId']

    }

    ngOnInit() {
        this.userform = this.fb.group({
            'paramName': new FormControl('', [Validators.pattern(/^[^\s]*$/), Validators.required]),
            'paramValue': new FormControl('', [Validators.pattern(/^[^\s]*$/), Validators.required]),
            'description': new FormControl('', [Validators.pattern(/^[\u4e00-\u9fa5a-zA-Z0-9\u3002\uff01\uff0c\u3001\uff1b\uff1a\u201c\u201d\u2018\u2019\uff08\uff09\u300a\u300b\u300b\u3008\u3009\u3010\u3011\u300e\u300f\u300c\u300d\ufe43\ufe44\u3014]{1,255}$/), Validators.required])
        });
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    ngOnChanges() {
        this.fuzhi();

    }
    fuzhi() {
        this.commonParamsBean = Object.assign({}, this.inValue);
    }
    //提交
    preserveClick() {
        //校验
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        // //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        this.commonHttpService.modifyCommonParamService(this.commonParamsBean).subscribe(data => {
            if (data.returnCode.code == "success") {
                if (this.commonParamsBean.paramName == 'XWLoginTimeout') {
                    this.TimeOut()
                }
                this.outValue.emit(false);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务错误" });
        });

    }
    //取消
    modifyCloseBtn() {
        this.outValue.emit(false);
        this.commonParamsBean = new CommonParamsBean();
    }
    TimeOut() {
        let enable;
        if (this.commonParamsBean.status == 'stop') {
            enable = 0;
        } else {
            enable = 1;
        }
        this.commonHttpService.resetTimeout({ timeout: this.commonParamsBean.paramValue, enable: enable }).subscribe()
    }

}