import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { ChangePasswordBean } from './bean/change-password.bean'
import { CommonHttpService } from './../../http/common.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';

@Component({
    selector: 'change-password',
    styleUrls: ['change-password.scss'],
    templateUrl: 'change-password.html',
    providers: [CommonHttpService, Commfunc]
})

export class ChangePassword implements OnInit {
    @Input() inValue: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter();
    changePasswordBean: ChangePasswordBean = new ChangePasswordBean()
    //提示信息
    msgs: Message[] = [];
    precredit:string = '用户密码修改'
    constructor(
        public commonHttpService: CommonHttpService,
        public commfunc: Commfunc
    ) {

    }

    ngOnInit() {

    }
    //确定修改密码
    confirm() {
        let ch = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        this.changePasswordBean.userId = ch['userId'];
        this.changePasswordBean.tokenNo = ch['tokenNo'];
        this.changePasswordBean.loginOrgId = ch['orgId'];
        this.commonHttpService.changePassword(this.changePasswordBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                window.history.go(-1)
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败" });
        })
    }
    cancel() {
        window.history.go(-1)
        this.changePasswordBean = new ChangePasswordBean();
    }
}