import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { BaseManagePages } from './base-manage.component';
import { PartyManage } from './party-manage/party-manage';
import { RoleManage } from './role-manage/role-manage';
import { PostsManage } from './posts-manage/posts-manage';
import { PowerManage } from './power-manage/power-manage';
import { AdministerManage } from './administer-manage/administer-manage';
import { AdministerTree } from './administer-tree/administer-tree';
// import { TeamManage } from './team-manage/team-manage';
import { ChangePassword } from './change-password/change-password';
import { ReleasePassword } from './release-password/release-password';
import { OperateLog } from './operate-log/operate-log';

import { PartyAllComponent } from './party-all/party-all.component';//机构岗位管理父页面
import { PostAssignmentComponent } from './post-assignment/post-assignment.component';//分配岗位
import { BusinessFieldComponent } from './business-field/business-field.component';//业务字段
import { WebSourceComponent } from "app/pages/tzb/common/base-manage/web-app-source/web-app-source.component";
import { CommonParamsComponent } from './common-params/common-params.component';
import { ExceptionTransactionListQueryComponent } from './exception-transaction-list-query/exception-transaction-list-query.component';

export const routes: Routes = [
    {
        path: '',
        component: BaseManagePages,
        children: [
            { path: 'user-manage', loadChildren: './user-manage/user-manage.module#UserManageModule' },
            { path: 'party-manage', loadChildren: './party-manage/party-manage.module#PartyManageModule' },
            { path: 'role-manage', loadChildren: './role-manage/role-manage.module#RoleManageModule' },
            { path: 'posts-manage', loadChildren: './posts-manage/posts-manage.module#PostsManageModule' },
            { path: 'power-manage', loadChildren: './power-manage/power-manage.module#PowerManageModule' },
            { path: 'administer-manage', loadChildren: './administer-manage/administer-manage.module#AdministerManageModule' },
            { path: 'administer-tree', component: AdministerTree },
            { path: 'team-manage', loadChildren: './team-manage/team-manage.module#TeamManageModule' },
            { path: 'change-password', component: ChangePassword },
            { path: 'release-password', component: ReleasePassword },
            { path: 'operate-log', component: OperateLog },
            { path: 'data-authority', loadChildren: "./data-authority-manage/data-authority-manage.module#DataAuthoManageModule" },
            { path: 'party-all', loadChildren: "./party-all/party-all.module#PartyAllModule" },
            { path: 'post-assignment', loadChildren: "./post-assignment/post-assignment.module#PostAssignmentComponentModule" },
            { path: 'business-field', loadChildren: './business-field/business-field.module#BusinessFieldModule' },
            { path: 'web-source', component: WebSourceComponent },
            { path: 'logQuery', loadChildren: './logQuery/logQuery.module#LogQueryModule' },
            { path: 'common-params', component: CommonParamsComponent },
            { path: 'business-center-manage', loadChildren: './business-center-manage/business-center-manage.module#BusinessCenterManageModule' },
            { path: 'perssion-manage', loadChildren: './permission-manage/permission-manage.module#PermissionManageModule' },
            { path: 'operation-change-record', loadChildren: './operationChangeRecord/operationChangeRecord.module#operationChangeRecordModule' },
            { path: 'exception-transaction-list-query', component: ExceptionTransactionListQueryComponent }
        ]
    }
];

export const BasekManageRouting: ModuleWithProviders = RouterModule.forChild(routes);
