import { Component, OnInit, OnChanges } from '@angular/core';
import { CommonParamsBean } from './bean/common-params.bean';
// import { DealHttpService } from '../../http/deal.http.service';
import { ConfirmationService } from 'primeng/primeng';
import { Message, SelectItem } from 'primeng/primeng';
import { checkName } from 'app/@uisftech/common/service/validator';
// import { Status, ParamGroupId, ParamTypeId } from '../../constant/codeValue';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CommonHttpService } from '../../http/common.http.service';
//公共参数
@Component({
    selector: 'common-params',
    templateUrl: './common-params.component.html',
    styleUrls: ['./common-params.component.scss'],
    providers: [CommonHttpService, ConfirmationService, Commfunc]
})
export class CommonParamsComponent implements OnInit {
    tabMesg: any = tableMessage;// 表格无数据时显示的信息
    commonParamsTitle: string = '公共参数管理';//标题
    // 表体内容
    tableData: any;
    commonParamsBean: CommonParamsBean = new CommonParamsBean()
    //角色状态
    roleStatus: any = [];
    //筛选 状态
    status: any = [];
    //总页数
    total: string;
    //每页多少条
    limit: string;

    //提示信息
    msgs: Message[] = [];

    //模态框的标题
    headerTitle: string = "修改";
    //模态框是否显示
    display: boolean = false;
    showModel: string;

    //传出的值
    inValue: string;

    //修改的参数
    updateValue: any = [];

    //参数项
    detailValue: string;
    //公共模块 字段所属模块
    paramGroupId: any = [];
    //公共模块 字段所属类型
    paramTypeId: any = [];
    rows: any;
    currentPage: any;
    first: number = 0;//分页控制
    code: any;
    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
        public commfunc: Commfunc
    ) {
        this.codeValues() //调用方法，获取全部码值
        this.status = this.code['Status'];
        this.paramGroupId = this.code['ParamGroupId'];
        this.paramTypeId = this.code['ParamTypeId']
        // this.tableData=[];
        // this.tableData.push({label:'paramName', value:'paramName'});
    }

    ngOnInit() {

        this.commonParamsBean.limit = '10';
        this.commonParamsBean.page = '1';
        // this.commonParamsBean.status='start',
        // this.commonParamsBean.paramGroupId='PMS'
        this.query();

        //按回车键查询
        let _thiss = this;
        document.onkeydown = function (event) {
            if (event && event.keyCode === 13) {
                _thiss.query();
            }
        }
    }

    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //列表查询
    query() {
        this.commonHttpService.queryCommonParamService(this.commonParamsBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.returnCommonParamList;
                this.total = data.total;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }
    queryClick() {//点击查询
        this.first = 0;
        this.commonParamsBean.page = '1';
        this.commonHttpService.queryCommonParamService(this.commonParamsBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.tableData = data.returnCommonParamList;
                this.total = data.total;
                // this.msgs = [];
                // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
        })
    }

    //分页
    paginate(event) {
        this.rows = event.rows;
        //每页显示的条数
        this.commonParamsBean.limit = event.rows;
        //当前页
        this.currentPage = event.page + 1 + '';
        this.commonParamsBean.page = this.currentPage;
        this.first = event.rows * event.page;
        //调用查询的方法
        this.query();
        // this.commonParamsBean.page = '1';
    }

    //修改
    update(item) {
        this.headerTitle = '参数修改';
        this.display = true;
        this.showModel = '2';
        this.updateValue = item;
    }

    //修改的回调
    updateCall(param) {
        this.display = param;
        this.query();
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: '修改成功' });
    }

    //删除
    delete(item) {
        this.confirmationService.confirm({
            message: '确认要删除吗?',
            accept: () => {
                // 条件
                let param = { paramId: item.paramId };
                this.commonHttpService.deleteCommonParamService(param).subscribe(data => {
                    if (data.returnCode.code == "success") {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                        this.query();
                    } else {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
                    }
                }, error => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: "删除失败" });
                })
            }
        });
    }

    //添加
    add() {
        this.headerTitle = '参数增加';
        this.display = true;
        this.showModel = '1';
    }

    //添加后返回的值
    addCall(param) {
        this.display = param;
        // this.roleManageBean.startRow = '1';
        this.query();
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: '新增成功' });
    }

    // 重置
    reset() {
        this.commonParamsBean.paramName = '';
        this.commonParamsBean.status = '';
        this.commonParamsBean.paramTypeId = '';
        this.commonParamsBean.paramGroupId = '';
    }

    //公共参数项
    detail(item) {
        this.headerTitle = '参数项';
        this.display = true;
        this.showModel = '3';
        this.detailValue = item;
        //   

    }

    detailCall(param) {
        this.display = param;
        //   this.query();
    }

    //返回的值
    outValue(param) {
        this.display = param.display;
    }
    //按钮权限
    permissionCheck(id) {
        return this.commfunc.permissionCheck(id)
    }

}
