import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { OperateLogBean } from './bean/operate-log.bean';
import { CommonHttpService } from '../../http/common.http.service';
import { Message } from 'primeng/primeng';
import { ConfirmationService } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';



@Component({
    selector: 'operate-log',
    templateUrl: 'operate-log.html',
    providers: [ConfirmationService]
})

export class OperateLog implements OnInit {
    // 表格中显示的信息
    tabMesg: any = tableMessage;
    precredit: string = '当日操作日志查询';
    // 表体内容
    tableData: any;
    operateLogBean: OperateLogBean = new OperateLogBean()

    //总页数
    total: string;
    //每页多少条
    pageSize: string = '10';

    //提示信息
    msgs: Message[] = [];


    constructor(
        public commonHttpService: CommonHttpService,
        public confirmationService: ConfirmationService,
    ) { }

    ngOnInit() {

        this.operateLogBean.pageSize = '10';
        this.operateLogBean.pageNumber = '1';
        this.queryClick();
    }

    //查询
    queryClick() {
 
    }


    // 重置
    // 重置
    reset() {
        this.operateLogBean = new OperateLogBean()
    }
}